.about {
  max-width: 800px;
  margin: 100px auto;
  font-size: 1.5em;
  text-align: center;
  
  &-img {
    position: relative;

    img {
      max-width: 150px;
      margin-bottom: 50px;
      position: absolute;
      cursor: pointer;
      animation: animateLogo 10s infinite;
    }
  }

  &-wrapper-p {
    padding-top: 200px;

    p {
      padding-top: 12px;

      a {
        font-weight: bold
      }
    }
  } 
}

@keyframes animateLogo {
  0% {
    top: 0px;
    right: calc(50% - 75px);
  }

  16% {
    top: 0;
    right: 100%;
  }

  32% {
    top: 400px;
    right: 100%;
  }

  48% {
    top: 400px;
    right: 40%;
  }

  64% {
    top: 400px;
    right: -20%;
  }

  80% {
    top: 0;
    right: -20%;
  }
  
  100% {
    right: calc(50% - 75px);
  }
}
