.homeNotConnected {
  text-align: center;
  font-size: 2em;
  margin-top: 100px;

  p {
    margin-bottom: 20px;
  }
}

.homeConnected {
  display: flex;
  justify-content: space-around;
  align-items: center;

  .homeConnected-wrapper {
    margin-top: 100px;
    border: 2px solid black;
    border-radius: 10px;
    padding: 40px 20px;
    background-color: #eee;
  
    ul li {
      font-size: 1.5em;
      margin-left: 20px;
      line-height: 2em;
  
      span {
        font-weight: bold;
      }
    }
  }

  h2 {
    font-weight: bold;
    font-size: 1.7em;
    text-decoration: underline;
    padding-bottom: 20px;
  }

  .favoris, .userRepoData {
    margin-left: 20px;
    border: 2px solid black;
    border-radius: 10px;
    padding: 40px 20px;
    font-size: 1.5em;
    background-color: #eee;

  }
}

