.list {
  margin: 1em 0;
  font-size: 1.5em;
  display: flex;
  flex-direction: column;
  align-items: center;

  &-content {
    margin-top: 30px;
    display: flex;
    align-items: center;

    a {
      margin-left: 10px;
      padding: 10px 10px;
      border-radius: 10px;
      border: 1px solid black;
    }
  }

  .item {
    display: flex !important;
    justify-content: center !important;
  }
  
  .icon {
    margin-top: 10px;
    cursor: pointer;
  }
}
