.wrapperTable {
  margin: 1em auto;
  width: 50%;

  button {
    background-color: yellow;
    margin-left: 20px;
    padding: 10px;
    border-radius: 10px;
    border: none;
  }
  
  .link {
    margin: 1em 0;
    background-color: black;
    border-radius: 10px;
    color: white;
    cursor: pointer;
    padding: 10px;
  }
  
  .wrapper-contentReadMe {
    border: 1px solid #d1d5da;

    .contentReadMe-title {
      padding: 7px;
      font-weight: bold;
      background-color: #F6F8FA;
      border-bottom: 1px solid #d1d5da;
    }

    .contentReadMe {
      margin: 3em 0;
      padding: 0 50px;
      
      img {
        max-width: 100%;
      }
      
      h1 {
        font-size: 2em;
        font-weight: bold;
        margin: 10px 0;
      }

      h2 {
        font-size: 1.7em;
        font-weight: bold;
        margin: 10px 0;
      }

      h3 {
        font-size: 1.4em;
        font-weight: bold;
        margin: 10px 0;
      }

      h4 {
        font-size: 1.2em;
        font-weight: bold;
        margin: 10px 0;
      }

      strong {
        font-weight: bold;
      }

      a {
        color: #4266d7;
        
        &:hover {
          text-decoration: underline;
        }
      }

      ul li {
        list-style: inside;
        padding-left: 15px;
      }

      pre {
        background-color: #F6F8FA !important;
        padding: 20px !important;
      }
    }
  }
}



